/* Global Styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  background-color: #f9f9f9;
  color: #333;
}

/* Navbar Container */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

/* Logo */
.logo {
  display: flex;
  align-items: center;
}

.logo svg {
  width: 150px;  /* Adjust width as needed */
  height: auto;
}

/* Logo */
.logo a {
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
  color: #007aff;
}

/* Regular Navigation Links (Desktop Only) */
.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-links li a {
  text-decoration: none;
  font-size: 16px;
  color: #333;
  transition: color 0.3s ease;
}

.nav-links li a:hover {
  color: #007aff;
}

/* Burger Menu Icon */
.burger-menu {
  display: none;  /* Hidden on desktop */
  font-size: 28px;
  cursor: pointer;
}

/* Slide-in Mobile Menu (Hidden by Default on Desktop) */
.side-menu {
  display: none;
}

@media (max-width: 768px) {
  /* Show the burger icon on mobile */
  .burger-menu {
    display: block;  /* Show the burger menu on mobile */
    margin-left: auto;  /* Align the burger menu to the right */
  }

  .nav-links {
    display: none!important;  /* Hide regular nav links on mobile */
  }

  /* Show the side menu on mobile */
  .side-menu {
    position: fixed;
    top: 0;
    left: -100%;  /* Initially offscreen */
    height: 100%;
    width: 250px;
    background-color: #fff;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
    transition: left 0.3s ease;  /* Smooth slide-in */
    z-index: 1001;
    display: block;  /* Make sure the side-menu is shown */
  }

  .side-menu.open {
    left: 0;  /* Slide in when open */
  }

  .side-menu ul {
    list-style: none;
    padding: 20px;
  }

  .side-menu ul li {
    margin-bottom: 20px;
  }

  .side-menu ul li a {
    font-size: 18px;
    color: #333;
    text-decoration: none;
    transition: color 0.3s ease;
  }

  .side-menu ul li a:hover {
    color: #007aff;
  }
}

/* Regular Navigation Links (Desktop Only) */
.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-links li a {
  text-decoration: none;
  font-size: 16px;
  color: #333;
  transition: color 0.3s ease;
}

.nav-links li a:hover {
  color: #007aff;
}

.nav-links li .cta-button {
  padding: 10px 20px;
  background-color: #007aff;
  color: #fff;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 500;
  transition: background-color 0.3s ease;
}

.nav-links li .cta-button:hover {
  background-color: #005ecb;
}

/* CTA Button */
.cta-button {
  width: 100%;
  padding: 14px;
  font-size: 16px;
  color: #fff!important;
  background-color: #007aff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #005ecb;
}

/* Hero Section */
.hero-section {
  width: 100%;
  height: 400px;  /* Adjust height as needed */
  background: linear-gradient(135deg, #007aff, #00e4ff);  /* Colorful background */
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  padding: 0 20px;  /* Add padding to ensure text doesn't touch the edges */
  box-sizing: border-box;  /* Include padding in width calculation */
  overflow-x: hidden;  /* Prevent horizontal overflow */
}

/* Hero Content */
.hero-content {
  max-width: 1080px; /* Restrict width for readability */
  width: 100%;  /* Make sure it doesn't overflow */
  box-sizing: border-box;
  padding: 0 20px; /* Ensure internal padding is accounted for */
}

.hero-section h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.hero-section p {
  font-size: 1.25rem;
  line-height: 1.6;
  margin: auto;
  max-width: 800px;
}

/* Container */
.container {
  max-width: 480px;
  margin: 50px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

/* Heading */
.container h2 {
  font-size: 26px;
  margin-bottom: 15px;
  text-align: center;
  font-weight: 600;
  color: #333;
}

.container p {
  font-size: 14px;
  color: #666;
  text-align: center;
  margin-bottom: 25px;
}

/* Form Group */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
  color: #444;
}

.input-field {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fafafa;
  transition: border 0.3s ease;
}

.input-field:focus {
  border: 1px solid #007aff;
  outline: none;
}

/* Button */
.button {
  width: 100%;
  padding: 14px;
  font-size: 16px;
  color: #fff;
  background-color: #007aff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #005ecb;
}

.button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Salary Display */
.salary-display {
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  color: #333;
}

/* Slider container to keep the input and value inline */
.slider-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
  margin-bottom: 20px; /* Add margin-bottom to create space between the slider and the button */
  gap: 10px;  /* Add gap between the slider and the value */
}

/* Style the slider (range input) */
input[type="range"] {
  -webkit-appearance: none;  /* Override default look */
  appearance: none;
  width: 100%;
  height: 8px;
  background: linear-gradient(to right, #007aff 0%, #007aff 0%, #ddd 0%, #ddd 100%);  /* Progress fill effect */
  border-radius: 5px;
  outline: none;
  opacity: 0.7;
  transition: background 0.2s ease-in;  /* Smooth transition for fill */
}

input[type="range"]:hover {
  opacity: 1;  /* Full opacity on hover */
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;  /* Override default look */
  appearance: none;
  width: 20px;
  height: 20px;
  background: #007aff;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);  /* Soft shadow for thumb */
}

input[type="range"]::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #007aff;
  border-radius: 50%;
  cursor: pointer;
}

/* Ensure the blue fill updates dynamically */
input[type="range"].slider-progress {
  background: linear-gradient(to right, #007aff var(--progress), #ddd var(--progress));  /* Dynamically controlled fill */
}

/* Styling the slider value (years) */
.slider-value {
  font-size: 16px;
  color: #333;
  font-weight: 500;
  margin-left: 10px;
  width: 50px;
  text-align: right;
  flex-shrink: 0;  /* Prevent the value text from shrinking */
}

/* Salary Display */
.salary-display {
  margin-top: 20px;
  font-size: 24px;
  font-weight: normal;  /* Set normal weight for text */
  text-align: center;
  color: #333;  /* Set text color to black */
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);  /* Soft shadow for depth */
  transition: transform 0.3s ease;
}

.salary-display p {
  margin: 0;
}

.salary-display p span {
  font-weight: bold;  /* Bold the numerical value */
  color: #0500e0;  /* Slightly highlight the number in blue */
}

/* FAQ Section */
.faq-section {
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  text-align: left;
}

.faq-section h2 {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 30px;
  color: #333;
}

.faq-list {
  border-top: 1px solid #ddd;
}

.faq-item {
  border-bottom: 1px solid #ddd;
  padding: 20px 0;
  cursor: pointer;  /* Make the entire item clickable */
  transition: background-color 0.3s ease;
}

.faq-item:hover {
  background-color: #f0f0f0;  /* Add a subtle hover effect */
}

.faq-question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.25rem;
  font-weight: 500;
  color: #333;
  transition: color 0.3s ease;
}

.faq-item.active .faq-question {
  color: #007aff;  /* Change color when active */
}

/* Plus/Minus Icon */
.faq-icon {
  position: relative;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.faq-icon::before,
.faq-icon::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #007aff;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.faq-icon::before {
  transform: rotate(0); /* Vertical line of plus */
}

.faq-icon::after {
  transform: rotate(90deg); /* Horizontal line of plus */
}

.faq-item.active .faq-icon::before {
  opacity: 0; /* Hide vertical line to turn plus into minus */
}

.faq-item.active .faq-icon::after {
  transform: rotate(0); /* Horizontal line becomes minus */
}

/* FAQ Answer */
.faq-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease, opacity 0.4s ease;
  opacity: 0;
}

.faq-answer p {
  font-size: 1rem;
  color: #666;
  line-height: 1.6;
  padding: 10px 0;
}

.faq-item.active .faq-answer {
  opacity: 1;
  max-height: 500px; /* Adjust height as needed */
}

/* Footer Styles */
.footer {
  width: 100%;
  padding: 20px;
  background-color: #f1f1f1;
  text-align: center;
  color: #333;
  font-size: 14px;
  margin-top: 50px;
  box-sizing: border-box;  /* Include padding in width calculation */
  overflow-x: hidden;  /* Prevent horizontal overflow */
}

/* Footer Links */
.footer-links {
  margin-top: 10px;
  display: inline-block;  /* Ensure the links don't stretch */
  box-sizing: border-box;  /* Include padding in width */
}

.footer a {
  color: #007aff;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.footer a:hover {
  color: #005ecb;
}

.footer-links span {
  margin: 0 5px;
  color: #999;
}

/* Responsive Navbar */
@media (max-width: 768px) {
  .navbar {
    display: flex;
    justify-content: space-between;  /* Ensure logo on left and menu on right */
    align-items: center;
    padding: 15px 20px;
  }

  .nav-links {
    flex-direction: column;
    gap: 10px;
  }

  .cta-button {
    width: 100%;  /* Ensure button takes full width on mobile */
    margin-top: 10px;
    text-align: center;
  }

  .logo a {
    font-size: 20px; /* Slightly reduce logo font size for mobile */
  }

  .logo svg {
    width: 120px;  /* Adjust width for mobile */
  }
}

/* Responsive Hero Section */
@media (max-width: 768px) {
  .hero-section {
    height: auto;  /* Allow height to adjust based on content */
    padding: 40px 20px;
  }

  .hero-section h1 {
    font-size: 2rem;
    word-wrap: break-word;  /* Ensure long words break and don't cause overflow */
  }

  .hero-section p {
    font-size: 1rem;
    max-width: 100%;
    word-wrap: break-word;
  }
}

/* Responsive Salary Calculator */
@media (max-width: 768px) {
  .container {
    padding: 20px 15px;  /* Ensure consistent padding on mobile */
    max-width: calc(100% - 44px);  /* Prevent the container from touching the screen edges */
  }

  .form-group label {
    font-size: 14px;  /* Adjust label size for mobile */
  }

  .input-field {
    padding: 10px;
    font-size: 14px;  /* Slightly smaller input field text for mobile */
  }

  .button {
    padding: 12px;
    font-size: 14px;  /* Reduce button size slightly for mobile */
  }

  .slider-container {
    flex-direction: column;  /* Stack the slider and value vertically on mobile */
    gap: 5px;  /* Adjust gap for stacked layout */
  }

  .slider-value {
    text-align: left;  /* Align value to the left on mobile */
    font-size: 14px;  /* Adjust text size */
  }
}

/* Responsive FAQ Section */
@media (max-width: 768px) {
  .faq-section {
    padding: 20px 15px;  /* Adjust padding for mobile */
    max-width: 100%;  /* Ensure full width on mobile */
  }

  .faq-section h2 {
    font-size: 1.75rem;  /* Reduce heading size slightly */
  }

  .faq-question {
    font-size: 1.125rem;  /* Slightly smaller font size for mobile */
  }

  .faq-answer p {
    font-size: 0.9rem;  /* Reduce paragraph text size for mobile */
  }
}

/* Responsive Footer */
@media (max-width: 768px) {
  .footer {
    padding: 20px 15px;
    box-sizing: border-box;
  }

  .footer p {
    font-size: 12px;
  }

  .footer-links {
    flex-direction: column;
    gap: 5px;
  }

  .footer-links a {
    font-size: 14px;
  }
}